import { Button, LinearProgress, Dialog, DialogActions, DialogContent, Slide } from "@material-ui/core"
import { Alert, InputAdornment } from '@mui/material'
import TextField from '@mui/material/TextField'
import axios from "axios"
import React, { useState } from "react"
import { useForm } from "react-hooks-helper"


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />
})

const Login = () => {
    const [open, setOpen] = useState(false)
    const [oky, setOkey] = useState(false)
    const [line, setLine] = useState(false)
    const [message, setMessage] = useState('')

    const [formData, setForm] = useForm({
        email: '',
        password: ''
    })
    const { email, password } = formData

    const handleOpen = () => { setOpen(true) }
    const handleClose = () => { setOpen(false) }

    return (
        <div>
            {line ? <LinearProgress style={{ marginTop: '0px', marginBottom: '0px' }} /> : ''}
            <h4 style={{ margin: '10px' }}>Login</h4>
            <hr></hr>

            <div style={{ boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', textAlign: 'justify', padding: '25px', borderRadius: '5px' }}>

                <TextField
                    id="outlined"
                    name="email"
                    label='Usuario'
                    value={email}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>Email</strong></InputAdornment>
                    }}
                />

                <br></br>
                <TextField
                    id="outlined"
                    name="password"
                    type="password"
                    label='Senha'
                    value={password}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '10px', width: '300px', marginRight: '0px' }}
                />

                <br></br>

                {oky ? <Alert color="success">Usurio acedeu com sucesso</Alert> : ''}

                <Button
                    color="primary"
                    variant="contained"
                    style={{ textTransform: 'none', borderRadius: '2px', marginRight: '5px', marginTop: '15px', boxShadow: 'none' }}
                    onClick={() => {
                        setLine(true)
                        axios.post('/api/user/login', { email: formData.email, password: formData.password }).then(res => {
                            console.log(res)
                            if (res.data.status === "success") {
                                setOkey(true)
                                setTimeout(() => {
                                    window.location.href = '/'
                                }, 500)
                            } else {
                                setLine(false)
                                setOkey(false)
                                setMessage('As cedncias fornecidas esto erradas')
                                handleOpen()
                            }
                        }).catch(e => {
                            setLine(false)
                            setOkey(false)
                            setMessage('As cedncias fornecidas estas erradas')
                            handleOpen()
                        })
                    }}
                >Login</Button>  <br></br>

                <hr></hr>
                <Button
                    color="primary"
                    variant="contained"
                    style={{ textTransform: 'none', borderRadius: '2px', marginTop: '0px', marginBottom: '30px',boxShadow: 'none' }}
                    onClick={() => { window.location.href = '/registar'}}>
                    Registar
                </Button> 

            </div>

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogContent>
                    <h5>Log</h5>
                    <hr></hr>
                    <span style={{ textTransform: 'uppercase', color: 'red' }}>{message}</span>
                    <hr></hr>
                </DialogContent>
                <DialogActions>
                    <Button
                        color="secondary"
                        variant="contained"
                        style={{ float: 'left', boxShadow: 'none' }}
                        onClick={handleClose}
                    >Fechar</Button>
                </DialogActions>
            </Dialog>


        </div>
    )
}

export default Login 