import { Navigate, Route, Routes } from "react-router-dom"
import axios from "axios"
import './assets/css/App.css'
import { useEffect, useState } from "react"
import Home from "./pages/Home"
import Loger from "./pages/Loger"
import ErrorPage from "./pages/ErrorPage"
import Login from "./pages/Login"
import Dashboard from "./pages/Dashboard"
import About from "./pages/About"
import Sign from "./pages/Sign"

const App = () => {
  const [loading, setLoading] = useState(true)
  const [user, setUser] = useState({})
  const [isLoggedIn, setLoggedIn] = useState(false)

  useEffect(() => {
    axios.get('/api/user/me').then(res => {
      console.log(res);
      if (res.data.status === 'success') {
        if (res.data.data.data.role === 'user') {
          setUser(res.data.data.data)
          setLoggedIn(true)
          setLoading(false)
        }
      }
    }).catch(e => {
      const err = { e }
      setLoggedIn(false)
      setUser({ email: "", role: "", username: "" })
      setLoading(false)
    })
  }, [])


  return (
    <div className="container">
      <Routes>
        <Route path='/' element={loading ? <Loger /> : <Home user={user} isLoggedIn={isLoggedIn} />} />
        <Route path='/login' element={loading ? <Loger /> : isLoggedIn ? <Navigate replace to="/" /> : <Login />} />
        <Route path='/dashboard' element={loading ? <Loger /> : isLoggedIn ? <Dashboard user={user} /> : <Navigate replace to="/" /> } />
        <Route path='/sobre' element={<About user={user} isLoggedIn={isLoggedIn}  />} />
        <Route path='/registar' element={<Sign />} />
        <Route path='*' element={<ErrorPage />} />
        
      </Routes>
    </div>
  )
}

export default App
