import TextField from '@mui/material/TextField'
import { Alert, Autocomplete, InputAdornment } from '@mui/material'
import { useEffect } from "react"
import { useForm } from "react-hooks-helper"
import { Button, LinearProgress, Slide } from '@material-ui/core'
import axios from 'axios'
import Dialog from '@mui/material/Dialog'
import DialogContent from '@mui/material/DialogContent'
import DialogTitle from '@mui/material/DialogTitle'
import React, { useState } from "react"
import CurrencyTextField from '@unicef/material-ui-currency-textfield'
import { Grid, Typography } from '@material-ui/core'


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />
})

function gerador(length) {
    var result = '';
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
        result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
}

export const PostItems = ({ user }) => {
    const [open, setOpen] = useState(false)
    const [error, setError] = useState('')
    const [ms, setMs] = useState('')
    const handleOpen = () => { setOpen(true) }
    const handleClose = () => { setOpen(false) }
    const [value, setValue] = React.useState('');

    const [formData, setForm] = useForm({
        owner: user.telefone,
        product: '',
        price: '',
        description: ''
    })

    const {
        owner,
        product,
        price,
        description
    } = formData

    const submitItem = () => {
        const item = {
            owner: formData.owner,
            product: formData.product,
            price: value,
            description: formData.description,
            data: new Date().toLocaleString()
        }


        axios.post('/api/item/submit-item', item).then(response => {
            console.log(response)
            if (response.data.status === 'success') {
                handleOpen()
                setTimeout(() => {
                    setMs('Venda anunciada com sucesso!')
                    window.location.href = '/dashboard'
                }, 2000)
            }
            if (response.data.e.message) {
                setError(response.data.e.message)
                alert("Caro usurio, preencha todos os campos!\n" + response.data.e.message)
            }
        }).catch(e => {
            console.log(e)
        })
    }

    return (
        <div style={{ marginBottom: '100px' }}>
            <h5>Anunciar venda</h5>
            <hr></hr>

            <TextField
                id="outlined-multiline-static"
                label="Denominao do produto"
                name="product"
                onChange={setForm}
                size='small'
                value={product}
                style={{ marginBottom: '10px', width: '350px', marginRight: '10px' }}
                placeholder="Produto"
            />
            <br></br>
            <TextField
                id="outlined-multiline-static"
                label="Descrio do produto"
                name="description"
                onChange={setForm}
                value={description}
                multiline
                rows={3}
                style={{ marginBottom: '30px', width: '350px', marginRight: '10px' }}
                placeholder="Descrio"
                helperText={<span style={{ color: 'green', fontSize: '13px', border: '1px solid silver' }}>Por favor, fornea-nos uma descrio do produto que pretende vender!</span>}
            />

            <Grid item xs={12} md={4}>
                <CurrencyTextField
                    size="small"
                    variant="outlined"
                    value={value}
                    label="Preo/Ms"
                    currencySymbol="#"
                    style={{ marginBottom: '10px', width: '250px', marginRight: '10px' }}
                    onChange={(event, value) => setValue(value)}
                    InputProps={{
                        endAdornment: <InputAdornment position="end" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>(MZN)</strong></InputAdornment>
                    }}
                    helperText={<span style={{ color: 'green', fontSize: '13px', border: '1px solid silver' }}>Por favor, fornea-nos o preo do produto!</span>}
                
                />
            </Grid>
         

            <hr></hr>
            
            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', marginTop: '10px' }}
                onClick={submitItem}
            >
                Sumeter item
            </Button>

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                {ms === '' ? <LinearProgress></LinearProgress> : ''}
                <DialogContent>
                    {ms === '' ? <strong>A anunciar venda...</strong > : <strong style={{ color: 'green' }}>{ms}</strong>}
                </DialogContent>
            </Dialog>

        </div>
    )
} 