import { useEffect } from "react"
import Header from "../components/Header"
import { Items } from "../components/Items"
import Footer from "../components/Footer"

const Home = ({ user, isLoggedIn }) => {
    return (
        <div>
            <Header user={user} isLoggedIn={isLoggedIn} />
            <Items />
            <Footer />
        </div>
    )
}
export default Home