import Header from "../components/Header"
import Footer from "../components/Footer"

const About = ({ user, isLoggedIn }) => {
    return (
        <div>
            <Header user={user} isLoggedIn={isLoggedIn} />
            <h5>Sobre</h5>
            <hr></hr>
            <p style={{boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', color: 'white', textAlign: 'justify', backgroundColor: 'dodgerblue',padding: '25px',borderRadius: '5px'}}>
                <strong><span style={{fontSize: '32px'}}>ARENDAR EM TETE</span></strong>  uma plataforma electrnica orientada a promoo imobiliria de residncias localizadas nos bairros urbanos da cidade de Tete.  Foi desenvolvida com o intuito de intermediar clientes e proprietrios que desejam alugar ou arrendar patrimnios.
                A plataforma oferece solues flexveis e interfaces intuitivas que permitem aos proprietrios:
                <ul style={{marginLeft: '30px', marginTop: '10px'}}>
                    <li>Anunciar seus patrimnios;</li>
                	<li>Fazer uso de servios de alerta de solicitaes por SMS;</li>
                	<li>Anunciar contedos publicitrios de produtos ou marcas.</li>
                </ul>
            </p>
            <Footer />
        </div>
    )
}

export default About 