import { Button, CircularProgress, Dialog, DialogActions, DialogContent, Slide } from "@material-ui/core"
import axios from "axios"
import React, { useEffect, useState } from "react"
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import { Alert } from "@mui/material"

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />
})

export const MyItems = () => {
    const [rows, setRows] = useState([])
    const [itemID, setItemID] = useState('')
    const [show, setShow] = useState(true)
    const [open, setOpen] = useState(false)
    const [red, setRed] = useState({})
    const [user, setUser] = useState({})
    const [openB, setOpenB] = useState(false)
    const [request, setRequest] = useState([])

    const handleOpen = () => { setOpen(true) }
    const handleClose = () => { setOpen(false) }

    const handleOpenB = () => { setOpenB(true) }
    const handleCloseB = () => { setOpenB(false) }

    // `/api/item/get-my-items/${user.telefone}`
    useEffect(() => {
        axios.get('/api/user/me').then(res => {
            if (res.data.status === 'success') {
                if (res.data.data.data.role === 'user') {

                    axios.get(`/api/item/get-my-items/${res.data.data.data.telefone}`).then(res => {
                        console.log(res);
                        if (res.data.status === "success") {
                            setRows(res.data.data)
                            setShow(false)

                            for (let index = 0; index < res.data.data.length; index++) {
                                axios.get(`/api/item/get-request/${res.data.data[index]._id}`).then(result => {
                                    if (result.data.status === "success") {
                                        if (result.data.data.length == 3) {
                                            setRed({ color: true, id: res.data.data[index]._id })
                                        }
                                        if (result.data.data.length > 3) {
                                            axios.delete(`/api/item/delete-item/${res.data.data[index]._id}`).then(res => {
                                                window.location.href = '/dashboard'
                                            })
                                        }
                                    }
                                })
                            }


                        }

                    })
                }
            }
        })
    }, [])



    return (
        <div>
            <h5>Meus items</h5>
            <hr></hr>
            {rows.length === 0 ? '' : <Alert color='warning'>
                <span>Veja ordens de compra em {'>'} <strong>Ordens</strong></span>
                </Alert>}
            {show ? <div style={{ textAlign: 'center' }}>
                <div style={{ marginLeft: 'auto', marginRight: 'auto', width: '50px', marginTop: '50px' }}>
                    <CircularProgress />
                </div> <span style={{ color: 'green' }}>A carregar items</span> <br></br>
                <span style={{ color: 'green', fontSize: '12px' }}>Por favor aguarde ...</span>
            </div> :
                <div>
                    {rows.length === 0 ? <span style={{ color: 'silver' }}><em>Sem items anunciados</em></span> : <div className='grid-content-view-B'>
                        {rows.map(el => (<div className="card-view-B"> {red.color && el._id === red.id ? <p style={{ height: '10px', backgroundColor: 'coral' }}></p> : ''}
                            <p style={{ margin: '5px' }}>
                            <strong>Produto</strong>: <span style={{ color: 'green', fontSize: '14px' }}>{el.product}</span> <br></br>
                                <strong>Preo</strong>: <span style={{ color: 'green', fontSize: '14px' }}>{el.price} (MZN)</span> <br></br>
                                <strong>Descrio</strong>: <span style={{ color: 'green', fontSize: '14px' }}>{el.description}</span> <br></br>
                                <strong>Data de anuncio</strong>: <span style={{ color: 'green', fontSize: '14px' }}>{el.data}</span>
                                <hr></hr>
                            </p>
                            <Button
                                color="secondary"
                                variant="contained"
                                style={{ textTransform: 'none', borderRadius: '0px', margin: '3px', boxShadow: 'none' }}
                                onClick={() => {
                                    setItemID(el._id)
                                    handleOpenB()
                                }}
                            >Eliminar item</Button>
                            <Button
                                color="primary"
                                variant="contained"
                                style={{ textTransform: 'none', borderRadius: '0px', float: 'right', margin: '3px', boxShadow: 'none' }}
                                onClick={() => {
                                    axios.get(`/api/item/get-request/${el._id}`).then(res => {
                                        if (res.data.status === "success") {
                                            setRequest(res.data.data)
                                        }
                                        handleOpen()
                                    })
                                }}
                            >Ordens</Button>
                        </div>))}
                    </div>}
                </div>}

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogContent>
                    {request.length == 0 ? <div>
                        <hr></hr>
                        <span style={{ color: 'red' }}>/*Sem solicitao</span>
                    </div> : <div> <strong style={{color: request.length == 3 ? 'coral' : 'green'}}>Solicitaes ({request.length})</strong> 
                    <Button 
                    color="secondary"
                    variant="contained"
                    style={{float: 'right', boxShadow: 'none'}}
                    onClick={handleClose}
                    >Fechar</Button>
                        <hr></hr>
                        {request.map(el => (<div>
                            <AccountCircleIcon /> <strong>{el.telefone}</strong> | <span style={{marginRight: '20px'}}>{el.data}</span>
                            <Button 
                                color="primary"
                                variant="contained"
                                style={{ boxShadow: 'none', marginTop: '0px', float: 'right'}}
                                onClick={() => window.location.href = `tel:${el.telefone}`}>
                                    Ligar
                            </Button>
                            <hr></hr>
                        </div>))}
                        <Alert color='warning'>Este item ser removido da platadorma aps (3) solicitaes</Alert>
                    </div>
                    }
                </DialogContent>
            </Dialog>


            <Dialog
                open={openB}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleCloseB}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogContent>
                  <span>DESEJA ELIMINAR ESTE ITEM DA PLATAFORMA ?</span>
                </DialogContent>
                <DialogActions>
                <Button 
                    color="secondary"
                    variant="contained"
                    style={{float: 'left', boxShadow: 'none'}}
                    onClick={handleCloseB}
                    >Cancelar</Button>
                    <Button 
                    color="primary"
                    variant="contained"
                    style={{float: 'right', boxShadow: 'none'}}
                    onClick={() => {
                        //delete here
                        axios.delete(`/api/item/delete-item/${itemID}`).then(res => {
                            console.log(res)
                            window.location.href = '/dashboard'
                        })
                    }}
                    >SIM</Button>
                </DialogActions>
            </Dialog>
        </div>
    )
} 