import { Button, LinearProgress, Dialog, DialogActions, DialogContent, Slide } from "@material-ui/core"
import { Alert, InputAdornment } from '@mui/material'
import TextField from '@mui/material/TextField'
import axios from "axios"
import React, { useState } from "react"
import { useForm } from "react-hooks-helper"


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />
})

const Sign = () => {
    const [open, setOpen] = useState(false)
    const [oky, setOkey] = useState(false)
    const [message, setMessage] = useState('')
    const [formData, setForm] = useForm({
        username: '',
        telefone: '',
        email: '',
        password: '',
        passwordConfirm: ''
    })

    const handleOpen = () => { setOpen(true) }
    const handleClose = () => { setOpen(false) }

    const { username, telefone, email, password, passwordConfirm } = formData

    return (
        <div>
            <p style={{ backgroundColor: 'coral', height: '3px' }}></p>
            <h4>Registar</h4> 
            <hr></hr>
            {oky ? <LinearProgress style={{ marginTop: '-16px', marginBottom: '20px' }} /> : ''}

            <div style={{ boxShadow: 'rgba(0, 0, 0, 0.24) 0px 3px 8px', color: 'white', textAlign: 'justify', padding: '25px', borderRadius: '5px' }}>
                <TextField
                    id="outlined"
                    name="username"
                    label='Nome do usurio'
                    value={username}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    required
                />
                <br></br>
                <TextField
                    id="outlined"
                    type="email"
                    name="email"
                    label='Email do usurio'
                    value={email}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    required
                />
                <br></br>
                <TextField
                    id="outlined"
                    type="telefone"
                    name="telefone"
                    label='Telefone do usurio'
                    value={telefone}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><strong style={{ color: 'green' }}>+258</strong></InputAdornment>
                    }}
                    required
                />
                <br></br>
                <TextField
                    id="outlined"
                    name="password"
                    type="password"
                    label='Senha'
                    value={password}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '20px', width: '300px', marginRight: '0px' }}
                    required
                />
                <br></br>
                <TextField
                    id="outlined"
                    name="passwordConfirm"
                    type="password"
                    label='Senha'
                    value={passwordConfirm}
                    onChange={setForm}
                    size="small"
                    style={{ marginBottom: '10px', width: '300px', marginRight: '0px' }}
                    required
                />
                <br></br>
                {oky ? <Alert color="success">Usurio criado com sucesso</Alert> : ''}
                <Button
                    color="primary"
                    variant="contained"
                    style={{ textTransform: 'none', borderRadius: '2px', marginTop: '16px', boxShadow: 'none' }}
                    onClick={() => {
                        console.log(formData);
                        if (formData.password === formData.passwordConfirm) {
                            axios.post('/api/user/signup', formData).then(res => {
                                console.log(res)

                                if (res.data.status === "success") {
                                    setOkey(true)
                                    setTimeout(() => {
                                        window.location.href = '/'
                                    }, 500); 
                                }

                                if (res.data.e) {
                                    setMessage(res.data.e.message)

                                    if(res.data.e.code == 11000){
                                        setMessage('Este usurio j foi registado')
                                    }
                                    
                                    handleOpen()
                                }
                            }).catch(e => { })
                        } else {
                            setMessage('As senhas fornecidas no so iguais')
                            handleOpen()
                        }
                    }}
                >Registar</Button>
            </div>

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogContent>
                    <h5>Log</h5>
                    <hr></hr>
                    <span style={{ textTransform: 'uppercase', color: 'red' }}>{message}</span>
                    <hr></hr>
                </DialogContent>
                <DialogActions>
                    <Button
                        color="secondary"
                        variant="contained"
                        style={{ float: 'left', boxShadow: 'none' }}
                        onClick={handleClose}
                    >Fechar</Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default Sign