import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import { Button } from "@material-ui/core"
import { MyItems } from "../components/MyItems"
import { PostItems } from "../components/PostItems"
import { Link } from 'react-router-dom'

const Dashboard = ({ user }) => {
    return (
        <div>
            <p style={{ backgroundColor: 'green', height: '3px' }}></p>
            <Link to={'/'} style={{ float: 'right', textDecoration: 'underline' }}>Ir para Pgina principal</Link>
            <h1>Dashboard</h1>
            <hr></hr>
            <strong>User: </strong> <span>{user.username}</span> <br></br>
            <strong>Email: </strong> <span>{user.email}</span> <br></br>
            <strong>Tel.: </strong> <span>{user.telefone}</span> <br></br>
            <hr></hr>
            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', marginRight: '5px', borderRadius: '2px', boxShadow: 'none' }}
                onClick={(e) => {
                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><MyItems /></React.StrictMode>)
                }}
            >Meus items</Button>
            <Button
                color="primary"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '2px', boxShadow: 'none' }}
                onClick={(e) => {
                    ReactDOM.createRoot(document.getElementById('desktop')).render(<React.StrictMode><PostItems user={user} /></React.StrictMode>)
                }}
            >Anunciar venda</Button>
            <hr></hr>
            <div id='desktop'>
                <MyItems />
            </div>
        </div>
    )
}

export default Dashboard  