import { Button } from "@material-ui/core"
import axios from "axios"
import { Link } from "react-router-dom"

const Header = ({ user, isLoggedIn }) => {

    return (
        <div> 
            <div>
                <a href="" style={{textDecoration: 'none'}}>
                    <span style={{fontSize: '40px'}}><span style={{backgroundColor: 'coral', padding: '5px 10px 0px 10px', color: 'white', borderRadius: '0px 0px 10px 10px'}}><strong>Market</strong></span>
                    <span style={{marginLeft: '5px'}}><strong>Place</strong></span>
                    </span>
                </a>
            </div>
            {isLoggedIn ? <Button
                color="secondary"
                variant="contained"
                style={{ textTransform: 'none', borderRadius: '2px', float: 'right', marginTop: '33px', boxShadow: 'none' }}
                onClick={() => {
                    axios.get('/api/user/logout').then(res => {
                        window.location.href = '/'
                    })
                }}
            >Sair</Button> : <nav style={{float: 'right'}}>
                <Link to='/registar'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '2px', marginTop: '33px'}}
                    >Registar</Button>
                </Link> <span style={{color: 'coral'}}> </span>
                <Link to='/login'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '2px', marginTop: '33px' }}
                    >LogIn</Button>
                </Link>
            </nav>

            }
            <hr></hr>

            <nav>
                {isLoggedIn ? <Link to='/dashboard'>
                    <Button
                        color="primary"
                        variant="contained"
                        style={{ textTransform: 'none', borderRadius: '2px', boxShadow: 'none' }}
                    >Anunciar venda</Button>
                </Link> : ""}
            </nav>
            <hr></hr>
        </div>
    )
}
export default Header